:root{scroll-behavior:smooth}:root body{background-color:#a0d1fe;margin:0;font-family:"PT Serif",serif;color:#445eae}.root-container{background-color:#f2f6fd;max-width:600px;width:100%;margin:auto;min-height:120vh;padding-bottom:5vh}header{display:flex;flex-direction:column;justify-content:space-between;align-items:center}header .header__para{margin-top:0;font-style:italic;padding-left:1rem}header .heading{font-family:Poppins,sans-serif;color:#191e3d;line-height:.8}.home{display:flex;justify-content:space-evenly;padding-top:2rem;padding-bottom:2rem}.home a{text-decoration:none;background-color:#445eae;box-shadow:0 5px 0 #192340;color:#fff;padding:.4rem 1rem;text-transform:uppercase;transition:background-color .3s ease-in-out;font-weight:500;border-radius:4px;position:relative}.home a:hover{background-color:#637ac2;cursor:pointer}.home a:active{box-shadow:none;top:5px}.home #moment{font-weight:500;font-family:sans-serif;font-style:oblique;align-self:center}.journal-inputs{display:flex;flex-direction:column;justify-content:space-between;align-items:center;padding-top:1.75rem}.journal-inputs .entry-title{padding:.5rem 1rem}.journal-inputs .entry-body,.journal-inputs .entry-title{max-width:340px;margin-bottom:1rem;font-size:1rem;font-family:sans-serif;font-weight:500;color:#444;line-height:1.3;border:1px solid #aaa;box-shadow:0 1px 0 1px rgba(0,0,0,.04);border-radius:.5em;background-color:#fff;box-sizing:border-box;width:100%}.journal-inputs .entry-body{padding:1rem}.todo-heading{font-family:Poppins,sans-serif;font-size:1.75rem;color:#191e3d;margin-top:4rem;margin-bottom:1.5rem;text-align:center}.todos__filters{display:flex;justify-content:space-evenly;flex-wrap:wrap}.todos__filters .todos__input{max-width:320px;margin-bottom:1rem;padding:.5rem 1rem;font-size:1rem;font-family:sans-serif;font-weight:500;color:#444;line-height:1.3;border:1px solid #aaa;box-shadow:0 1px 0 1px rgba(0,0,0,.04);border-radius:.5em;background-color:#fff;box-sizing:border-box;width:100%}.todos__filters .todos__checkbox{font-family:sans-serif;font-style:oblique;text-align:center;color:#1133e1}.todos__filters .todos__checkbox #todos__checkbox{margin-right:.35rem}.todos-root{background:#fff;padding:5vh 1.6rem}.todos-root .todo-card{display:flex;justify-content:space-around;align-items:center;padding:2rem 1.6rem;margin:1.5rem auto;border:1px dotted #888;background:#f2f6fd;transition:opacity .3s ease,background .3s ease}.todos-root .todo-card:hover{opacity:.8;background:#eee}.todos-root .todo-card .todo-card-div{max-width:350px;width:100%;display:flex;justify-content:flex-start;align-items:center;font-size:1.1rem;color:#191e3d;font-weight:500}.todos-root .todo-card .todo-card-div [type=checkbox]{margin-right:1rem}.todos-root .todo-card button{font-size:1rem;border:none;background:inherit;text-decoration:underline;position:relative}.todos-root .todo-card button:active{top:5px}.todo-checked{text-decoration:line-through;color:#888}#todos__form{display:flex;justify-content:space-between;flex-direction:column;align-items:flex-start;flex-wrap:wrap}.todos__form__input{max-width:340px;margin-bottom:1rem;padding:.5rem 1rem;font-size:1rem;font-family:sans-serif;font-weight:500;color:#444;line-height:1.3;border:1px solid #aaa;box-shadow:0 1px 0 1px rgba(0,0,0,.04);border-radius:.5em;background-color:#fff;box-sizing:border-box;width:100%}.submit-todo{background-color:#1133e1;color:#fff;padding:.6rem 1.5rem;position:relative;text-decoration:none;text-transform:uppercase;transition:background-color .3s ease-in-out;font-weight:600;border-radius:4px}.submit-todo:hover{background-color:#3554f0;cursor:pointer}.submit-todo:active{box-shadow:none;top:5px}.danger-zone{padding:10rem auto;display:flex;flex-direction:column;justify-content:space-evenly;align-items:center}.danger-zone h2{font-family:Poppins,sans-serif;font-size:1.75rem;color:red;margin-top:4rem;margin-bottom:1.5rem}.danger-zone .remove-entry{background-color:#1133e1;color:#fff;padding:.6rem 1.5rem;position:relative;text-decoration:none;text-transform:uppercase;transition:background-color .3s ease-in-out;font-weight:600;border-radius:4px}.danger-zone .remove-entry:hover{background-color:#3554f0;cursor:pointer}.danger-zone .remove-entry:active{box-shadow:none;top:5px}.todo-button{display:none;padding:.6rem 1.5rem;color:#fff;background:#de7c4a;position:fixed;top:30px;right:20px;border:none;border-radius:20px;font-size:.8rem;opacity:.8}@media (min-width:600px){.home a{padding:.4rem 2.25rem}}@media (min-width:450px){#todos__form{align-items:center;flex-direction:row}}@media (min-width:480px){#todos__form{justify-content:space-around}#todos__form [type=text]{margin-bottom:auto}}@media (min-width:600px){.todo-button{right:20vw}}@media (min-width:1000px){.todo-button{right:30vw}}